import { useEffect } from "react";
import { motion, useMotionValue, useSpring } from "framer-motion";

export const MouseTrail = () => {
    const x = useMotionValue(200);
    const y = useMotionValue(20);

   
    const springs = [      
        { x, y, opacity: 0.5 },       
        { x: useSpring(x, { stiffness: 150, damping: 20 }), y: useSpring(y, { stiffness: 150, damping: 20 }), opacity: 0.23 },
        { x: useSpring(x, { stiffness: 138, damping: 20 }), y: useSpring(y, { stiffness: 138, damping: 20 }), opacity: 0.21 },
        { x: useSpring(x, { stiffness: 126, damping: 20 }), y: useSpring(y, { stiffness: 126, damping: 20 }), opacity: 0.19 },
        { x: useSpring(x, { stiffness: 114, damping: 20 }), y: useSpring(y, { stiffness: 114, damping: 20 }), opacity: 0.17 },
        { x: useSpring(x, { stiffness: 102, damping: 20 }), y: useSpring(y, { stiffness: 102, damping: 20 }), opacity: 0.15 },
        { x: useSpring(x, { stiffness: 90, damping: 20 }), y: useSpring(y, { stiffness: 90, damping: 20 }), opacity: 0.13 },
        { x: useSpring(x, { stiffness: 78, damping: 20 }), y: useSpring(y, { stiffness: 78, damping: 20 }), opacity: 0.11 },
        { x: useSpring(x, { stiffness: 66, damping: 20 }), y: useSpring(y, { stiffness: 66, damping: 20 }), opacity: 0.09 },
        { x: useSpring(x, { stiffness: 54, damping: 20 }), y: useSpring(y, { stiffness: 54, damping: 20 }), opacity: 0.07 },
    ];


    useEffect(() => {
        const handleMove = (e) => {
            x.set(e.clientX);
            y.set(e.clientY);
        };
        window.addEventListener("mousemove", handleMove);
        return () => window.removeEventListener("mousemove", handleMove);
    }, [x,y]);

    return (
        <>
            {springs.map((s, i) => (
                <motion.div
                    key={i}
                    style={{
                        position: "fixed",
                        width: 12,
                        height: 12,
                        borderRadius: "50%",
                        pointerEvents: "none",
                        background: `rgba(255,255,255,${s.opacity})`,
                        x: s.x,
                        y: s.y,
                        transform: "translate(-50%, -50%)",
                    }}
                />
            ))}
        </>
    );
}
