import { useEffect, useState } from "react";
import logo from "../Logo.png";
import { ScrambleLetter } from "../effects/ScrambeLetter";

export const Navigation = ({ isLoaded }) => {
    const menuItems = [
        "Solutions",
        "Technology",
        "About",
        "Careers",
        "Resources",
        "Contact",
    ];

    const [isMobile, setIsMobile] = useState(false);
    const [open, setOpen] = useState(false);

    useEffect(() => {
        const onResize = () => {
            const mobile = window.innerWidth <= 769;
            setIsMobile(mobile);
            if (!mobile) setOpen(false);
        };

        onResize();
        window.addEventListener("resize", onResize);
        return () => window.removeEventListener("resize", onResize);
    }, []);

    return (
        <>
            <nav
                style={{
                    position: "fixed",
                    top: 0,
                    left: 0,
                    right: 0,
                    padding: "30px 40px",
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                    zIndex: 100,
                    borderBottom: "2px solid #ffffff10",
                    backdropFilter: 'blur(2px)',
                }}
            >
                <div
                    style={{
                        borderRadius: "4px",
                        display: "flex",
                        alignItems: "center",
                        overflow: "hidden",
                    }}
                >
                    <img
                        src={logo}
                        alt=""
                        style={{
                            transform: isLoaded ? "translateY(0)" : "translateY(100%)",
                            transition: "1000ms linear",
                        }}
                    />
                </div>

                {!isMobile && (
                    <ul
                        style={{
                            display: "flex",
                            gap: "30px",
                            listStyle: "none",
                            padding: 0,
                            margin: 0,
                        }}
                    >
                        {menuItems.map((item) => (
                            <li key={item}>
                                <ScrambleLetter text={item} />
                            </li>
                        ))}
                    </ul>
                )}

                {isMobile && (
                    <button
                        onClick={() => setOpen((v) => !v)}
                        style={{
                            width: 32,
                            height: 24,
                            display: "flex",
                            flexDirection: "column",
                            justifyContent: "space-between",
                            background: "none",
                            border: "none",
                            cursor: "pointer",
                            zIndex: 1001,
                        }}
                    >
                        <span style={burgerLine(open, 1)} />
                        <span style={burgerLine(open, 2)} />
                        <span style={burgerLine(open, 3)} />
                    </button>
                )}
            </nav>

            {isMobile && open && (
                <div
                    style={{
                        position: "fixed",
                        inset: 0,
                        background: "#000",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        zIndex: 99,
                    }}
                >
                    <ul
                        style={{
                            listStyle: "none",
                            padding: 0,
                            margin: 0,
                            display: "flex",
                            flexDirection: "column",
                            gap: "28px",
                            textAlign: "center",
                        }}
                    >
                        {menuItems.map((item) => (
                            <li key={item} onClick={() => setOpen(false)}>
                                <ScrambleLetter text={item} />
                            </li>
                        ))}
                    </ul>
                </div>
            )}
        </>
    );
};

const burgerLine = (open, index) => ({
    height: 3,
    width: "100%",
    background: "#fff",
    transition: "0.3s ease",
    transform:
        open && index === 1
            ? "rotate(45deg) translateY(14px)"
            : open && index === 2
                ? "scaleX(0)"
                : open && index === 3
                    ? "rotate(-45deg) translateY(-15px)"
                    : "none",
});
