import { useCallback } from "react"
import Particles from "react-particles"
import { loadSlim } from "tsparticles-slim"

export const ParticleBackground = () => {
    const particlesInit = useCallback(async (engine) => {
        await loadSlim(engine)
    }, [])

    return (
        <Particles
            id="tsparticles"
            init={particlesInit}
            style={{
                position: "fixed",
                top: 0,
                left: 0,
                width: "100%",
                height: "100%",
                zIndex: 0,
            }}
            options={{
                background: {
                    color: {
                        value: "transparent",
                    },
                },
                particles: {
                    number: {
                        value: 800,
                        density: {
                            enable: true,
                            value_area: 800,
                        },
                    },
                    color: {
                        value: ["#ffffff", "#ff99cc", "#cc66ff", "#6699ff"],
                    },
                    shape: {
                        type: "circle",
                    },
                    opacity: {
                        value: 0.6,
                        random: true,
                    },
                    size: {
                        value: 2,
                        random: true,
                    },
                    move: {
                        enable: true,
                        speed: 0.5,
                        direction: "right",
                        random: false,
                        straight: false,
                        outModes: {
                            default: "out",
                        },
                        attract: {
                            enable: false,
                        },
                    },
                    wobble: {
                        enable: true,
                        distance: 60,
                        speed: 10,
                    },
                },
                interactivity: {
                    detectsOn: "canvas",
                    events: {
                        onHover: {
                            enable: true,
                            mode: "repulse",
                        },
                        resize: true,
                    },
                    modes: {
                        repulse: {
                            distance: 100,
                            duration: 0.4,
                        },
                    },
                },
                detectRetina: true,
            }}
        />
    )
}

